	module.exports = {"data":{"markdownRemark":{"html":"<p>If you like me develop Back End Project locally and want it run on c9, this may help you.</p>\n<ol>\n<li>Push your project to github</li>\n<li>Create a new workspace in c9 and set ‘Clone from Git or Mercurial URL (optional)’ with your github repository URL. After submit your create page, c9 will clone your project for you.</li>\n<li>Run in your c9 terminal window</li>\n</ol>\n<p><code>bash echo \"export NODE_PATH=$NODE_PATH:/home/ubuntu/.nvm/v0.10.35/lib/node_modules\" >> ~/.bashrc &#x26;&#x26; source ~/.bashrc</code> </p>\n<ol>\n<li>Run <code>bower install &#x26;&#x26; sudo npm install</code></li>\n<li>Run <code>mkdir data &#x26;&#x26; echo 'mongod --bind_ip=$IP --dbpath=data --nojournal --rest \"$@\"' > mongod &#x26;&#x26; chmod a+x mongod &#x26;&#x26; ./mongod</code></li>\n<li>Run <code>grunt serve</code> in another terminal window</li>\n</ol>","fields":{"slug":"/miscellaneous/develop-back-end-projects-locally-and-run-on-c9/"},"frontmatter":{"title":"Develop Back End Projects Locally and Run on C9"}}},"pathContext":{"slug":"/miscellaneous/develop-back-end-projects-locally-and-run-on-c9/"}}

